/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 */
.fnugg_resort_ed {
	padding: 0;
	color: #101010;
	margin: 0 0 10px 0;
}

.fnugg_resort_ed .resort_name {
	color: #fff;
	background-color: #181818;
	padding: 10px;
	font-weight: 800;
	font-size: 18px;
	border-radius: 5px 5px 0 0;
	margin: 0;
}

.fnugg_resort_ed .img_holder {
	position: relative;
	margin: 0;
}

.fnugg_resort_ed .img_holder .resort_thumb {
	width: 100%;
	height: 400px;
	display: block;
	object-fit: cover;
	object-position: center center;
}

.fnugg_resort_ed .img_holder .updated_date {
	position: absolute;
	bottom: 0;
	width: 100%;
	background-color: rgb(0 0 0 / 50%);
	color: #fff;
	text-align: center;
	padding: 6px 0;
	margin: 0;
}

.fnugg_resort_ed .img_holder .updated_date p {
	margin: 0;
	padding: 0;
	font-weight: 400;
	font-size: 14px;
	text-align: center;
}

.fnugg_resort_ed .img_holder .updated_date p.label {
	font-weight: 800;
	font-size: 17px;
}

.fnugg_resort_ed .text_holder {
	display: flex;
	flex-wrap: wrap;
	margin-top: -5px;
	background-color: #fff;
	align-content: center;
	align-items: center;
	border: 1px solid #f1f1f1;
	border-top: none;
	border-radius: 0 0 5px 5px;
	text-align: center;
}

.fnugg_resort_ed .text_holder .resort_detail {
	flex: 50%;
	padding: 25px 0;
	text-align: center;
}

.fnugg_resort_ed .text_holder .resort_detail p {
	font-size: 16px;
	line-height: 1.6;
	color: #101010;
	margin: 0;
	text-align: center;
}

.fnugg_resort_ed .text_holder .resort_detail.weather p {
	font-size: 90px;
	line-height: 1;
}

.fnugg_resort_ed .text_holder .resort_detail img {
	width: 32px;
}

.fnugg_resort_ed .text_holder .resort_detail.wind img {
	display: inline-block;
	vertical-align: middle;
	width: 14px;
	margin-right: 5px;
	margin-top: -4px;
}

.fnugg_resort_ed .text_holder .resort_detail.wind p.mps {
	display: inline-block;
}

.fnugg_resort_ed .text_holder .resort_detail.ski_path img {
	display: inline-block;
	vertical-align: middle;
}

.fnugg_resort_ed .text_holder .resort_detail.ski_path p {
	display: inline-block;
}

@media (min-width: 120px) and (max-width: 1023px) {

	.fnugg_resort_ed .text_holder .resort_detail.weather p {
		font-size: 60px;
		margin-top: 10px;
	}
}
